<template>
<footer>
	<ul class="flex">
		<li v-for="(item,index) in footerList" class="flex1">
			<router-link :to="item.path"
				v-bind:class="{footerActive:($route.path === item.path)}">
				<div class="iconfont" 
					v-bind:class="[item.icon]"></div>
				{{item.text}}
			</router-link>
		</li>
	</ul>
</footer>
</template>

<script>
export default {
	name:'footer',
	data:function() {
		return {
			footerList:[{
				path:'/',
				text:'快速购票',
				icon:'icon-lvyou'
			},{
				path:'/group',
				text:'定制',
				icon:'icon-menpiaogoumai'
			},{
				path:'/hotel',
				text:'酒店',
				icon:'icon-jiudian'
			},{
				path:'/user',
				text:'个人中心',
				icon:'icon-geren'
			}]
		}
	}
}
</script>

<style scoped="scoped">
footer {
	position: fixed;
	width: 100%;
	left: 0;
	bottom: 0;
}
footer ul {
	background: #fff;
}
footer ul li {
	
}
footer ul li a {
	display: block;
	font-size: 4.3vw;
	text-align: center;
	color: #000;
	padding: 10px 0;
}
footer ul li a div {
	font-size: 4.3vw;
}
footer ul li a div:before {
	font-size: 5.3vw;
	/*color: #000;*/
}
.footerActive {
	color:#eb9c45;
}
</style>


